"use client";

import React from "react";
import { useCustomizer } from "../hooks/useCustomizer";
import CustomizerSidebar from "../components/demo/CustomizerSidebar";
import LandingNavbar from "../components/demo/LandingNavbar";
import HeroSection from "../components/demo/HeroSection";
import MarqueeSection from "../components/demo/MarqueeSection";
import AboutSection from "../components/demo/AboutSection";
import MissionSection from "../components/demo/MissionSection";
import WhyChooseUsSection from "../components/demo/WhyChooseUsSection";
import NicheSection from "../components/demo/NicheSection";
import FAQSection from "../components/demo/FAQSection";
import TestimonialsSection from "../components/demo/TestimonialsSection";
import ContactSection from "../components/demo/ContactSection";
import FooterSection from "../components/demo/FooterSection";
import { PenTool } from "lucide-react";

export default function DemoPage() {
  const customizer = useCustomizer();
  const {
    activeFontPair,
    theme,
    showCustomizer,
    setShowCustomizer,
    customBrand,
  } = customizer;

  return (
    <div
      style={{
        "--font-heading": `'${activeFontPair.heading}', sans-serif`,
        "--font-body": `'${activeFontPair.body}', sans-serif`,
      }}
      className="min-h-screen bg-white selection:bg-indigo-100 selection:text-indigo-900 overflow-x-hidden"
    >
      {/* Mobile Notice */}
      <div className="lg:hidden fixed bottom-6 left-6 right-6 z-[100] bg-slate-900 border border-slate-700 p-4 rounded-2xl shadow-2xl flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div
            className={`w-10 h-10 rounded-xl ${theme.bg} flex items-center justify-center text-white shadow-lg`}
          >
            <PenTool size={20} />
          </div>
          <div>
            <p className="text-white font-bold text-sm">Design on Desktop</p>
            <p className="text-slate-400 text-[10px] font-bold uppercase tracking-wider">
              For best experience
            </p>
          </div>
        </div>
      </div>

      {/* Floating Action Button for Customizer */}
      {!showCustomizer && (
        <button
          onClick={() => setShowCustomizer(true)}
          className={`fixed right-6 bottom-6 z-50 ${theme.bg} text-white p-5 rounded-full shadow-2xl hover:scale-110 active:scale-95 transition-all group overflow-hidden ${theme.shadow}`}
        >
          <div className="absolute inset-0 bg-white/20 translate-y-full group-hover:translate-y-0 transition-transform duration-300"></div>
          <PenTool className="relative z-10" size={24} />
        </button>
      )}

      {/* Components */}
      <CustomizerSidebar {...customizer} />

      <LandingNavbar
        customBrand={customBrand}
        theme={theme}
        isMenuOpen={customizer.isMenuOpen}
        setIsMenuOpen={customizer.setIsMenuOpen}
        activeBrandId={customizer.activeBrandId}
      />

      <main>
        <HeroSection customBrand={customBrand} theme={theme} />
        <MarqueeSection customBrand={customBrand} theme={theme} />
        <AboutSection
          sectionDesigns={customizer.sectionDesigns}
          customBrand={customBrand}
          theme={theme}
        />
        <MissionSection
          sectionDesigns={customizer.sectionDesigns}
          customBrand={customBrand}
          theme={theme}
        />
        <WhyChooseUsSection
          sectionDesigns={customizer.sectionDesigns}
          activeBrandId={customizer.activeBrandId}
          theme={theme}
        />
        <NicheSection
          sectionDesigns={customizer.sectionDesigns}
          activeBrandId={customizer.activeBrandId}
          getNicheConfig={customizer.getNicheConfig}
          customBrand={customBrand}
          theme={theme}
        />
        <TestimonialsSection
          sectionDesigns={customizer.sectionDesigns}
          theme={theme}
        />
        <ContactSection customBrand={customBrand} theme={theme} />
        <FAQSection
          sectionDesigns={customizer.sectionDesigns}
          customBrand={customBrand}
          theme={theme}
        />
      </main>

      <FooterSection customBrand={customBrand} theme={theme} />
    </div>
  );
}
